import React, { useEffect, useState } from "react";

// Axios
import Axios from "axios";
import { API_URL, API } from "utils/index";

import "./index.scss";

// Component
import { Carousel } from "antd";
import Card from "Component/card";

export default function ListRekomendasiDetailKelas(props) {
  const [data, setData] = useState([]);
  const [highligh, setHiglight] = useState([]);
  const [diskon, setDiskon] = useState([]);
  const [wishlist, setWishlist] = useState([]);
  const [kategori, setKategori] = useState([]);

  const getKelas = (kategori) => {
    Axios.get(API + "class")
      .then((data) => {
        console.log(kategori);

        let newData = data.data.class.filter(
          (item) => item.idcategories == kategori
        );
        setData(newData);
      })
      .catch((err) => console.log(err));
    Axios.get(API + "hilights")
      .then((data) => {
        setHiglight(data.data.hilights);
      })
      .catch((err) => console.log(err));

    Axios.get(API + "promotions/discounts").then((data) => {
      setDiskon(data.data);
    });

    Axios.get(API + "whislists/mywhislists", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => setWishlist(data.data.whislists))
      .catch((err) => console.log(err));
    Axios.get(API + "categories").then((data) => {
      let dataTemp = data.data.categories;
      setKategori(dataTemp);
    });
  };
  useEffect(() => {
    getKelas(props.kategori);
  }, [props.kategori]);
  // console.log(props.kategori);
  return (
    <div className="section">
      <div className="container">
        <h3 className="title-detail-kelas">
          Rekomendasi <span>Kelas</span>
        </h3>
        <div className="border-title" />
        <Carousel
          className="carousel-detailKelas"
          infinite={false}
          slidesToScroll={1}
          slidesToShow={4}
          autoplay
          dots={false}
          responsive={[
            {
              breakpoint: 1200,
              settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
              },
            },
            {
              breakpoint: 990,
              settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
              },
            },
            {
              breakpoint: 770,
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
              },
            },
          ]}
        >
          {data.map((item, i) => {
            return (
              <Card
                key={`kelas-${i}`}
                judul={item.name}
                instrukturPhoto={item.imagesinstructor}
                instrukturNama={item.instructor}
                instrukturRole={item.roleinstructor}
                rate={item.rating}
                jumMateri={10}
                jumJam={item.duration}
                harga={item.price}
                diskon={diskon.filter(
                  (itemDiscount) => itemDiscount.idclass == item.idclass
                )}
                benefit={highligh.filter(
                  (itemHighligh) => itemHighligh.idclass === item.idclass
                )}
                cover={item.images}
                kategori={kategori.filter(
                  (itemKategori) =>
                    itemKategori.idcategories == item.idcategories
                )}
                slug={item.slug}
                id={item.idclass}
                mitraPhoto={item.imagesmitra}
                mitraName={item.namemitra}
                wishlist={wishlist.filter(
                  (itemWishlist) => itemWishlist.idclass == item.idclass
                )}
              />
            );
          })}
        </Carousel>
      </div>
    </div>
  );
}
