const initialState = {
  dataUser: {},
  activeSidebar: false,
};

export default function userReducer(state = initialState, action) {
  switch (action.type) {
    case "SET_DATA_USER":
      return { ...state, dataUser: action.payload };
    case "SET_STATUS_SIDEBAR":
      return { ...state, activeSidebar: action.payload };
    default:
      return state;
  }
}
