import React, { useEffect, useState } from "react";
import "./index.scss";
import { Link } from "react-router-dom";
import Progress from "react-progressbar";

// Image
import ImgCard from "Assets/Images/img-card-progress.png";
import VideoIcong from "Assets/Images/video-icon.svg";
import ListMateriIcon from "Assets/Images/list-materi-icon.svg";
import Axios from "axios";
import { API_URL, API } from "utils/index";

export default function CardProgressKelas(props) {
  const [materies, setMateries] = useState("");
  const [subClass, setSubClass] = useState([]);
  const getSubClass = () => {
    Axios.get(API + "subclass")
      .then((data) => {
        let newData = data.data.subclass.filter(
          (item) => item.idclass === props.id
        );
        // setSubClass(newData);
        getMateries(newData[0].idsubclass);
      })
      .catch((err) => console.log(err));
  };

  const getMateries = (id) => {
    Axios.get(API + "materies", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => {
        let newData = data.data.materies.filter(
          (item) => item.idsubclass == id
        );
        setMateries(newData[0].slug);
      })
      .catch((err) => console.log(err));
  };
  useEffect(() => {
    getSubClass();
  }, []);
  return (
    <div className="card-progress" key={props.id}>
      <div className="img-card-progress">
        <img
          src={process.env.REACT_APP_PATH_API_IMAGES_COVER + props.img}
          alt={props.name}
          className="img-cover"
        />
      </div>
      <div className="konten-card-progress">
        <h4 className="title-card-progress">{props.name}</h4>
        {/* <div className="progress-bar-item">
          <Progress completed={50} />
          <h5>{`${50}%`}</h5>
        </div> */}
        <div className="konten-card-progress-info">
          {/* <div className="kontent-card-progress-info-item">
            <img src={VideoIcong} alt="" />
            <h5>{`1/${10} Video Selesai`}</h5>
          </div> */}
          {/* <div className="kontent-card-progress-info-item">
            <img src={ListMateriIcon} alt="" />
            <h5>{`3/${10} Kuis Selesai`}</h5>
          </div> */}
          <div className="btn-progress">
            {materies === "" ? (
              <button className="btn-progress-item disabled-button" disabled>
                Lanjut Kelas
              </button>
            ) : (
              <Link
                to={`/materi/video/${props.slug}/${materies}`}
                className="btn-progress-item"
              >
                Lanjut Kelas
              </Link>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
