import React, { Component } from "react";
import { API_URL, API } from "utils/index";
import Layout from "Component/layout/index";
import HeroKategori from "Component/heroKategori";
import Card from "Component/card";

import DataKelas from "JSON/kelas.json";

import { connect } from "react-redux";
import Axios from "axios";

import instructorPhoto from "Assets/Images/user-testimoni.png";

import { setKategori } from "Store/Action/kategoriAction";

class KategoriPage extends Component {
  state = {
    slugId: this.props.match.params.slugKategori,
    dataKelas: [],
    kategoriId: [],
    highligh: [],
    discount: [],
    kategori: [],
    wishlist: [],
    title: "",
  };

  getData = () => {
    Axios.get(API + "class")
      .then((data) => {
        this.setState({
          dataKelas: data.data.class.filter(
            (item) => item.idcategories === this.props.kategoriAktif
          ),
        });
        // console.log(data.data.class);
      })
      .catch((err) => console.log(err));

    Axios.get(API + "promotions/discounts")
      .then((data) => this.setState({ discount: data.data }))
      .catch((err) => console.log(err));

    Axios.get(API + "whislists/mywhislists", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => this.setState({ wishlist: data.data.whislists }))
      .catch((err) => console.log(err));
  };

  getKategori = () => {
    Axios.get(API + "categories")
      .then((data) => {
        this.setState({
          kategoriId: data.data.categories.filter(
            (item) => item.slug === this.state.slugId
          ),
          kategori: data.data.categories,
        });
        this.props.setKategori(this.state.kategoriId[0].idcategories);
      })
      .catch((err) => console.log(err));
  };

  getHighligh = () => {
    fetch(API + "hilights")
      .then((res) =>
        res
          .json()
          .then((data) => this.setState({ highligh: [...data.hilights] }))
      )
      .catch((err) => console.log(err));
  };

  async componentDidMount() {
    await this.getKategori();
    await this.getData();
    await this.getHighligh();
    // console.log("ini pas dipanggil");
  }

  async componentDidUpdate(prevProps, prevState) {
    // console.log(prevProps.kategoriAktif);
    if (prevProps.kategoriAktif != this.props.kategoriAktif) {
      // await this.getKategori();
      await this.getData();
      await this.getHighligh();
    }
    // console.log("dipanggil terus");
  }

  render() {
    const { kategoriAktif } = this.props;

    return (
      <Layout>
        <HeroKategori />
        <div className="container mb-5">
          <div className="row justify-content-center">
            {this.state.dataKelas.map((item, i) => {
              return (
                <div
                  className="col-12 col-xl-3 col-lg-3 col-md-4 d-flex justify-content-center"
                  key={`col-rek-${i}`}
                >
                  <Card
                    key={`kelas-${i}`}
                    judul={item.name}
                    instrukturPhoto={item.imagesinstructor}
                    instrukturNama={item.instructor}
                    instrukturRole={item.roleinstructor}
                    rate={item.rating}
                    jumMateri={10}
                    jumJam={item.duration}
                    harga={item.price}
                    diskon={this.state.discount.filter(
                      (itemDiscount) => itemDiscount.idclass == item.idclass
                    )}
                    benefit={this.state.highligh.filter(
                      (itemHighligh) => itemHighligh.idclass === item.idclass
                    )}
                    cover={item.images}
                    kategori={this.state.kategori.filter(
                      (itemKategori) =>
                        itemKategori.idcategories == item.idcategories
                    )}
                    slug={item.slug}
                    id={item.idclass}
                    wishlist={this.state.wishlist.filter(
                      (itemWishlist) => itemWishlist.idclass == item.idclass
                    )}
                    mitraPhoto={item.imagesmitra}
                    mitraName={item.namemitra}
                  />
                </div>
              );
            })}
          </div>
        </div>
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    kategoriAktif: state.kategoriReducer.kategoriAktif,
  };
};

const mapDispatchToProps = {
  setKategori,
};

export default connect(mapStateToProps, mapDispatchToProps)(KategoriPage);
