import React, { Component } from "react";
import Layout from "Component/layout/index";
import Logo from "Assets/Images/logo.png";

// Axios
import Axios from "axios";
import { Link, Redirect } from "react-router-dom";
import { API_URL, API } from "utils/index";

// Icon
import { RiCloseCircleFill } from "react-icons/ri";
import { AiFillEyeInvisible, AiFillEye } from "react-icons/ai";

// Image
import IllustrationRegis from "Assets/Images/illustration.png";

export default class RegisterPage extends Component {
  state = {
    notMatch: true,
    data: {
      name: "",
      role: "u",
      email: "",
      password: "",
      mobile: "",
      date_born: "",
      jobrole: "",
      address: "",
    },
    confirmStatus: false,
    successRegist: false,
    showPassword1: false,
    showPassword2: false,
  };

  postDataToApi = () => {
    Axios.post(API + "register", this.state.data)
      .then((res) => {
        // console.log(res);
        if (res.status == 200) {
          this.setState({ confirmStatus: true });
          setTimeout(() => {
            this.setState({ confirmStatus: false, successRegist: true });
          }, 3000);
        }
      })
      .catch((err) => {
        // console.log(err);
        this.setState({ notMatch: false });
        setTimeout(() => {
          this.setState({
            notMatch: true,
          });
        }, 2000);
      });
  };

  handleChange = (e) => {
    const data = { ...this.state.data };
    const name = e.target.name;
    const value = e.target.value;
    data[name] = value;

    this.setState({ data });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    this.postDataToApi();
  };

  handleShow = () => {
    this.setState({ showPassword: !this.state.showPassword });
  };
  // Calender
  handleFocusCalender = (e) => {
    e.currentTarget.type = "date";
  };

  handleBlurCalender = (e) => {
    e.currentTarget.type = "text";
  };
  render() {
    // console.log(this.state.data);
    if (this.state.successRegist) {
      return <Redirect to="/login" />;
    }
    return (
      <Layout kelasSaya>
        <div className="section-login-register">
          <div className="card-login">
            <div className="row row-login">
              <div className="col-5">
                <div className="side-backdrop">
                  <h5>
                    Tingkatkan <span>kemampuan</span> dan
                    <span> wawasan</span> dengan <span>Ekselen</span>
                  </h5>
                  <img src={Logo} alt="" />
                </div>
              </div>
              {this.state.confirmStatus ? (
                <div className="col">
                  <div className="regis-success text-center">
                    <div className="img-wrapper">
                      <img src={IllustrationRegis} alt="" />
                    </div>
                    <h4>Selamat akun anda sukses dibuat</h4>
                    <p>Silahkan login untuk mulai belajar</p>
                  </div>
                </div>
              ) : (
                <div className="col">
                  <div className="login-konten">
                    <div className="row justify-content-center mb-2">
                      <div className="col-6 col-lg-3 col-md-5 col-sm-6 justify-content-center d-flex">
                        <Link to="/login" className="btn btn-login" type="Link">
                          Masuk
                        </Link>
                      </div>
                      <div className="col-6 col-lg-3 col-md-5 col-sm-6 justify-content-center d-flex">
                        <Link
                          className="btn btn-login font-weight-bold"
                          type="Link"
                          to="/register"
                        >
                          Daftar
                        </Link>
                      </div>
                    </div>
                    {!this.state.notMatch && (
                      <div className="row justify-content-center">
                        <div className="col">
                          <h5 className="title-notmatch">
                            <RiCloseCircleFill /> Email atau Password anda
                            salah!
                          </h5>
                        </div>
                      </div>
                    )}
                    <div className="form-register">
                      <form onSubmit={this.handleSubmit}>
                        <div className="form-group">
                          <label for="name">
                            Nama Lengkap{" "}
                            <span
                              style={{
                                fontSize: "13px",
                                fontWeight: "300",
                              }}
                            >
                              (akan tertera pada sertifikat)*
                            </span>
                          </label>
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Nama Lengkap"
                            name="name"
                            onChange={this.handleChange}
                          />
                        </div>
                        <div className="wrapper-form">
                          <div className="form-group">
                            <label for="email">Email</label>
                            <input
                              type="email"
                              className="form-control"
                              placeholder="Email"
                              name="email"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group ml-2">
                            <label for="password">Kata Sandi</label>
                            <div className="wrapper-input-password">
                              <input
                                type={
                                  this.state.showPassword1 ? "text" : "password"
                                }
                                className="form-control"
                                placeholder={`Kata Sandi`}
                                name="password"
                                onChange={this.handleChange}
                              />
                              <button
                                className="button-show-pwd"
                                onClick={() =>
                                  this.setState({
                                    showPassword1: !this.state.showPassword1,
                                  })
                                }
                                type="button"
                              >
                                {this.state.showPassword1 ? (
                                  <AiFillEye />
                                ) : (
                                  <AiFillEyeInvisible />
                                )}
                              </button>
                            </div>
                          </div>
                        </div>
                        {/* Formlist 4 */}
                        <div className="wrapper-form">
                          <div className="form-group">
                            <label for="jobrole">Pekerjaan</label>
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Pekerjaan"
                              name="jobrole"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group ml-2">
                            <label for="email">Tanggal Lahir</label>
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Tanggal Lahir"
                              name="date_born"
                              onChange={this.handleChange}
                              onFocus={this.handleFocusCalender}
                              onBlur={this.handleBlurCalender}
                            />
                          </div>
                        </div>
                        {/* Formlist 5 */}
                        <div className="wrapper-form">
                          <div className="form-group">
                            <label for="address">Alamat</label>
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Alamat"
                              name="address"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group ml-2">
                            <label for="mobile">No Handphone</label>
                            <input
                              type="text"
                              className="form-control"
                              placeholder="No Handphone"
                              name="mobile"
                              onChange={this.handleChange}
                            />
                          </div>
                        </div>
                        {/* Formlist 6 */}
                        {/* <div className="wrapper-form">
                          <div className="form-group">
                            <label for="email">Alamat</label>
                            <input
                              type="email"
                              className="form-control"
                              placeholder="Alamat"
                              name="email"
                              // onChange={this.handleChange}
                            />
                          </div>
                        </div> */}

                        {/* <div className="form-group">
                          <label for="password">Konfirmasi Kata Sandi</label>
                          <input
                            type="password"
                            className="form-control"
                            placeholder="Kata Sandi"
                            // name="password"
                            // onChange={this.handleChange}
                          />
                        </div> */}
                        {/* <div className="form-group">
                          <label for="password">Konfirmasi Kata Sandi</label>
                          <input
                            type="password"
                            className="form-control"
                            placeholder="Konfimasi Kata Sandi"
                            name="confirm_password"
                            // onChange={this.handleChange}
                          />
                        </div> */}
                        <div className="form-check mb-4">
                          <input type="checkbox" className="form-check-input" />
                          <label for="" className="form-check-label">
                            Saya ingin menerima berita terbaru tentang Ekselen
                          </label>
                        </div>
                        <p
                          className="text-center "
                          style={{ fontSize: "13px" }}
                        >
                          Dengan mendaftar, Anda menyetujui{" "}
                          <Link to="/legal/syarat-ketentuan">
                            Ketentuan Penggunaan
                          </Link>{" "}
                          dan{" "}
                          <Link to="/legal/kebijakan-privasi">
                            Kebijakan Privasi
                          </Link>{" "}
                        </p>
                        <button className="btn btn-login-submit" type="submit">
                          Daftar
                        </button>
                      </form>
                    </div>
                    {/* <p className="text-center my-1" style={{ fontSize: "12px" }}>
                    Atau masuk dengan
                  </p>
                  <div className="row justify-content-center mb-2">
                    <div className="col">
                      <button className="btn btn-login-facebook">
                        Facebook
                      </button>
                    </div>
                    <div className="col">
                      <button className="btn btn-login-google">Google</button>
                    </div>
                  </div> */}
                    <p
                      className="text-center"
                      style={{ fontSize: "12px", fontWeight: "300", margin: 0 }}
                    >
                      Sudah memiliki akun? silahkan
                      <Link
                        to="/login"
                        className="btn"
                        type="button"
                        style={{
                          fontSize: "12px",
                          borderBottom: "1px solid #2f3c80",
                          borderRadius: 0,
                          padding: 0,
                        }}
                      >
                        Masuk
                      </Link>
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}
