import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import Axios from "axios";
import { API_URL } from "utils/index";

// Component
import Layout from "Component/layout";
import HeroKategori from "Component/heroKategori";
import Card from "Component/card";

export default function CareerPage() {
  const [dataKelas, setDataKelas] = useState([]);
  const [career, setCareer] = useState([]);
  const [kategori, setKategori] = useState([]);
  const [highligh, setHiglight] = useState([]);
  const [discount, setDiscount] = useState([]);
  const [wishlist, setWishlist] = useState([]);
  const dataUser = useSelector((state) => state.userReducer.dataUser);
  // get Data
  const getDataCareer = () => {
    Axios.get(API_URL + "trendings/careers")
      .then((data) => setCareer(data.data.dataKelas))
      .catch((err) => console.log(err));
  };

  const getDataKelas = () => {
    Axios.get(API_URL + "class")
      .then((data) => setDataKelas(data.data.class))
      .catch((err) => console.log(err));

    Axios.get(API_URL + "categories", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    }).then((data) => {
      let dataTemp = data.data.categories;
      setKategori(dataTemp);
    });

    Axios.get(API_URL + "class/hilights")
      .then((data) => {
        setHiglight(data.data.hilights);
      })
      .catch((err) => console.log(err));

    Axios.get(API_URL + "promotions/discounts")
      .then((data) => setDiscount(data.data))
      .catch((err) => console.log(err));

    Axios.get(API_URL + "whislists/mywhislists", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => {
        // if (dataUser != undefined) {
        // }
        let newData = data.data.whislists.filter(
          (item) => item.idusers == dataUser.idusers
        );
        setWishlist(newData);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    getDataCareer();
    getDataKelas();
  }, []);

  let newProduk = [];
  let produk = career.map((itemRek) =>
    dataKelas.filter((item) => {
      if (item.idclass == itemRek.idclass) {
        newProduk.push(item);
      }
    })
  );
  return (
    <Layout>
      <HeroKategori titleCategori="Career Ready Program" />
      <div className="container mb-5">
        <div className="row">
          {newProduk.length != 0 ? (
            newProduk.map((item, i) => {
              return (
                <div
                  className="col-12 col-xl-3 col-lg-3 col-md-4 d-flex justify-content-center"
                  key={`col-rek-${i}`}
                >
                  {/* <Card
                    key={`kelas-${i}`}
                    judul={item.name}
                    instrukturPhoto={item.imagesinstructor}
                    instrukturNama={item.instructor}
                    instrukturRole={item.roleinstructor}
                    rate={item.rating}
                    jumMateri={10}
                    jumJam={item.duration}
                    harga={item.price}
                    diskon={discount.filter(
                      (itemDiscount) => itemDiscount.idclass == item.idclass
                    )}
                    benefit={highligh.filter(
                      (itemHighligh) => itemHighligh.idclass === item.idclass
                    )}
                    cover={item.images}
                    kategori={kategori.filter(
                      (itemKategori) =>
                        itemKategori.idcategories == item.idcategories
                    )}
                    slug={item.slug}
                    id={item.idclass}
                    wishlist={wishlist.filter(
                      (itemWishlist) => itemWishlist.idclass == item.idclass
                    )}
                  /> */}
                  <Card
                    key={`kelas-${i}`}
                    judul={item.name}
                    instrukturPhoto={item.imagesinstructor}
                    instrukturNama={item.instructor}
                    instrukturRole={item.roleinstructor}
                    rate={item.rating}
                    jumMateri={10}
                    jumJam={item.duration}
                    harga={item.price}
                    diskon={discount.filter(
                      (itemDiscount) => itemDiscount.idclass == item.idclass
                    )}
                    benefit={highligh.filter(
                      (itemHighligh) => itemHighligh.idclass === item.idclass
                    )}
                    cover={item.images}
                    kategori={kategori.filter(
                      (itemKategori) =>
                        itemKategori.idcategories == item.idcategories
                    )}
                    slug={item.slug}
                    id={item.idclass}
                    mitraPhoto={item.imagesmitra}
                    mitraName={item.namemitra}
                    wishlist={wishlist.filter(
                      (itemWishlist) => itemWishlist.idclass == item.idclass
                    )}
                    // wishlist={false}
                  />
                </div>
              );
            })
          ) : (
            <h1>Loading ...</h1>
          )}
        </div>
      </div>
    </Layout>
  );
}
