import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Carousel, Row, Col } from "antd";
import Card from "Component/card";

import { API_URL } from "utils/index";

// Data
import Data from "JSON/landingPage.json";
import DataKelas from "JSON/kelas.json";

// Component Test
import Navbar from "Component/navbar";
import TestiLoading from "Component/testimonialCard/CardLoadingTestimoni";

import Dummy from "JSON/dummy.json";
import Kelas from "JSON/kelas.json";
import Axios from "axios";
// const { Header, Content, Footer } = Layout;
export default class ExamplePage extends Component {
  state = {
    benefit: [],
    classState: [],
  };

  componentDidMount() {
    Axios.get(API_URL + "class")
      .then((data) => this.setState({ classState: data.data.class }))
      .catch((err) => console.log(err));
  }
  render() {
    let { classState } = this.state;
    // console.log(this.state.benefit);
    // console.log(classState);
    return (
      <div className="section-example">
        <div className="container">
          <div className="row">
            <Carousel dots={false} infinite={false}>
              {this.state.classState.map((item, i) => {
                return (
                  <div className="col-md-4">
                    <Card
                      key={1}
                      judul={item.name}
                      instrukturPhoto={""}
                      instrukturNama={item.tutor}
                      instrukturRole={"Backend"}
                      rate={5}
                      jumMateri={3}
                      jumJam={2}
                      harga={"150.000"}
                      diskon={"50.000"}
                      benefit={this.state.benefit.filter(
                        (itemBenefit) => itemBenefit.idclass === item.idclass
                      )}
                      cover={""}
                      kategori={"teknologi"}
                      slug={"/"}
                    />
                  </div>
                );
              })}
            </Carousel>
          </div>
        </div>
      </div>
      // <div className="section-example">
      //   <TestiLoading />
      // </div>
      // <Navbar />
    );
  }
}
