import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import "./index.scss";
import Axios from "axios";
import { API_URL, API } from "utils/index";

export default function Index(props) {
  const [title, setTitle] = useState("default");

  const kategoriAktif = useSelector(
    (state) => state.kategoriReducer.kategoriAktif
  );

  useEffect(() => {
    Axios.get(API + "categories")
      .then((data) => {
        console.log(data.data);
        let tempData = data.data.categories.filter(
          (item) => item.idcategories == kategoriAktif
        );
        let newTempData = { ...tempData[0] };
        // console.log(newTempData);
        setTitle(newTempData.name);
      })
      .catch((err) => console.log(err));
  }, [kategoriAktif]);
  return (
    <div className="section">
      <div className="container-fluid hero-kategori d-flex align-items-center">
        <div className="container">
          <h3 className="title-kategori">
            Kelas {props.titleCategori ? props.titleCategori : title}
          </h3>
          <p className="desk-kategori">
            Ikuti berbagai program pelatihan berikut
          </p>
        </div>
      </div>
    </div>
  );
}
