import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Switch, Route } from "react-router-dom";
import Axios from "axios";
import { PrivateRoute } from "Auth/PrivateRoute";
import { API_URL } from "utils/index";
import {
  Home,
  Detail,
  Kategori,
  Error,
  Login,
  Register,
  Example,
  About,
  KelasSaya,
  MateriKelas,
  Profile,
  SK,
  Order,
  Maintenance,
  CareerReady,
  Contact,
  Transaction,
} from "Pages";

// Style Global
import "Assets/Scss/style.scss";

function App(props) {
  const isLogin = useSelector((state) => state.loginReducer.isLogin);
  return (
    <>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route
          exact
          path="/kategori/:slugKategori"
          component={Kategori}
        ></Route>
        <Route
          exact
          path="/kelas-detail/:slugDetailKelas"
          component={Detail}
        ></Route>
        <Route path="/login" component={Login}></Route>
        <Route path="/register" component={Register}></Route>
        <Route path="/about-ekselen" component={About}></Route>
        <Route path="/maintenance" component={Maintenance} />
        <Route path="/career-ready-program" component={CareerReady} />
        <Route path="/contact-us" component={Contact} />
        <PrivateRoute path="/profile" component={Profile} />
        <PrivateRoute component={KelasSaya} path="/kelas-saya/:slugKelas" />
        <PrivateRoute
          path="/materi/:type/:slugKelas/:slugMateri"
          component={MateriKelas}
        ></PrivateRoute>
        <PrivateRoute component={Order} path="/payment/order/:orderSlug" />
        <Route component={SK} path="/legal/:legalSlug" />
        <Route component={Transaction} path="/transaction" />
        <Route path="/example" component={Example}></Route>
        <Route component={Error}></Route>
      </Switch>
    </>
  );
}

export default App;
