import React, { Component } from "react";
import Layout from "Component/layout/index";
import Logo from "Assets/Images/logo.png";
import { Link, Redirect } from "react-router-dom";

// icon
import { RiCloseCircleFill } from "react-icons/ri";

// Redux
import { connect } from "react-redux";
import { setLoginStatus } from "Store/Action/loginAction";

// Axios
import Axios from "axios";
import { API_URL, API } from "utils/index";

class LoginPage extends Component {
  state = {
    notMatch: true,
    data: {
      email: "",
      password: "",
    },
  };

  postDataToApi = () => {
    Axios.post(API + "login", this.state.data)
      .then((res) => {
        console.log(res.data.access_token);
        localStorage.setItem("token", res.data.access_token);
        if (res.status == 200) {
          this.props.setLoginStatus(true);
        }
      })
      .catch((err) => {
        this.setState({ notMatch: false });
        // console.log(err);
        setTimeout(() => {
          this.setState({ notMatch: true });
        }, 2000);
      });
  };
  handleChange = (e) => {
    const newData = { ...this.state.data };
    const name = e.target.name;
    const value = e.target.value;
    newData[name] = value;
    this.setState({ data: newData });
  };

  handleSubmit = async (e) => {
    e.preventDefault();
    await this.postDataToApi();

    await console.log(this.state.notMatch);
  };
  render() {
    if (this.props.isLogin) {
      return <Redirect to="/" />;
    }

    return (
      <Layout kelasSaya>
        <div className="section-login-register">
          <div className="card-login">
            <div className="row row-login">
              <div className="col-5">
                <div className="side-backdrop">
                  <h5>
                    Tingkatkan <span>kemampuan</span> dan
                    <span> wawasan</span> dengan <span>Ekselen</span>
                  </h5>
                  <img src={Logo} alt="" />
                </div>
              </div>
              <div className="col">
                <div className="login-konten">
                  <div className="row justify-content-center">
                    <div className="col-6 col-lg-3 col-md-5 col-sm-6 justify-content-center d-flex">
                      <Link
                        to="/login"
                        className="btn btn-login font-weight-bold"
                        type="Link"
                      >
                        Masuk
                      </Link>
                    </div>
                    <div className="col-6 col-lg-3 col-md-5 col-sm-6 justify-content-center d-flex">
                      <Link
                        to="/register"
                        className="btn btn-login"
                        type="Link"
                      >
                        Daftar
                      </Link>
                    </div>
                  </div>
                  {!this.state.notMatch && (
                    <div className="row justify-content-center">
                      <div className="col">
                        <h5 className="title-notmatch">
                          <RiCloseCircleFill /> Email atau Password anda salah!
                        </h5>
                      </div>
                    </div>
                  )}
                  <div className="form-login">
                    <form onSubmit={this.handleSubmit}>
                      <div className="form-group">
                        <label for="email">Email</label>
                        <input
                          type="email"
                          className="form-control"
                          placeholder="Email"
                          name="email"
                          onChange={this.handleChange}
                        />
                      </div>
                      <div className="form-group">
                        <label for="password">Password</label>
                        <input
                          type="password"
                          className="form-control"
                          placeholder="Password"
                          name="password"
                          onChange={this.handleChange}
                        />
                      </div>
                      <div className="form-check mb-4">
                        <input type="checkbox" className="form-check-input" />
                        <label for="" className="form-check-label">
                          Ingat Saya
                        </label>
                      </div>
                      <button
                        type="submit"
                        className="btn btn-login-submit"
                        to="/"
                      >
                        Masuk
                      </button>
                    </form>
                  </div>
                  {/* <p className="text-center my-1" style={{ fontSize: "12px" }}>
                    Atau masuk dengan
                  </p>
                  <div className="row justify-content-center mb-2">
                    <div className="col">
                      <button className="btn btn-login-facebook">
                        Facebook
                      </button>
                    </div>
                    <div className="col">
                      <button className="btn btn-login-google">Google</button>
                    </div>
                  </div> */}
                  <p
                    className="text-center"
                    style={{ fontSize: "12px", fontWeight: "300", margin: 0 }}
                  >
                    Belum memiliki akun? Silahkan
                    <Link
                      to="/register"
                      className="btn"
                      type="button"
                      style={{
                        fontSize: "12px",
                        borderBottom: "1px solid #2f3c80",
                        borderRadius: 0,
                        padding: 0,
                      }}
                      onClick={this.handleLogin}
                    >
                      daftar
                    </Link>
                    terlebih dahulu
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return { isLogin: state.loginReducer.isLogin };
};

const mapDispatchToProps = {
  setLoginStatus,
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
