import React, { useEffect, useState } from "react";
import { API_URL, API } from "utils/index";
import Axios from "axios";
import { Route, Switch, NavLink } from "react-router-dom";
import "./index.scss";

import CardProgressKelas from "./CardProgressKelas";
import EmptyClass from "./EmptyClass";

export default function KelasSaya(props) {
  const [dataKelas, setDataKelas] = useState([]);

  useEffect(() => {
    // Axios.get(API + "class")
    //   .then((data) => setDataKelas(data.data.class))
    //   .catch((err) => console.log(err));

    Axios.get(API + "usersclass", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((res) => {
        setDataKelas(res.data.userclass);
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className="section">
      <div className="container">
        <div className="kelas-saya-page">
          <div className="sidebar-menu">
            <NavLink
              className="sidebar-menu-item "
              to="/kelas-saya/progress/semua-kelas"
              activeClassName="sidebar-menu-item-active"
            >
              Semua Kelas
            </NavLink>
            <NavLink
              className="sidebar-menu-item"
              to="/kelas-saya/progress/progress-kelas"
              activeClassName="sidebar-menu-item-active"
            >
              Progress
            </NavLink>
            <NavLink
              className="sidebar-menu-item"
              to="/kelas-saya/progress/lulus"
              activeClassName="sidebar-menu-item-active"
            >
              Lulus
            </NavLink>
          </div>
          <Switch>
            <Route path="/kelas-saya/progress/semua-kelas">
              <div className="konten-kelas-saya">
                {dataKelas.length == 0 ? (
                  <div>kelas kosong</div>
                ) : (
                  dataKelas.map((item, i) => {
                    const itemData = { ...item.order_details[0].classes[0] };
                    return (
                      <CardProgressKelas
                        name={itemData?.name}
                        img={itemData?.images}
                        slug={itemData?.slug}
                        id={itemData?.idclass}
                        key={`cardprogres-${i}`}
                      />
                      // <CardProgressKelas
                      //   name={item.name}
                      //   img={item.images}
                      //   slug={item.slug}
                      //   id={item.idclass}
                      //   key={`cardprogres-${i}`}
                      // />
                    );
                  })
                )}
              </div>
            </Route>
            <Route path="/kelas-saya/progress/progress-kelas">
              <EmptyClass />
            </Route>
            <Route path="/kelas-saya/progress/lulus">
              <EmptyClass />
            </Route>
          </Switch>
        </div>
      </div>
    </div>
  );
}
