import React, { useEffect, useState } from "react";
import { Layout } from "antd";
import Navbar from "../navbar";
import FooterC from "Component/footer";
import "./layout.scss";
import { useSelector, useDispatch } from "react-redux";

// Api
import { API_URL, API } from "utils/index";

// Data
import Data from "JSON/landingPage.json";
import Axios from "axios";

export default function Index(props) {
  const isLogin = useSelector((state) => state.loginReducer.isLogin);
  const active = useSelector((state) => state.userReducer.activeSidebar);
  const [nameUser, setNameUser] = useState("");
  const [kategoriData, setKategoriData] = useState([]);
  const dispatch = useDispatch();

  const getDataKategori = () => {
    Axios.get(API + "categories")
      .then((data) => setKategoriData(data.data.categories))
      .catch((err) => console.log(err));
  };

  const getUser = () => {
    Axios.get(API + "profile", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => {
        // console.log(data.data);
        if (
          data.data.status != "Token is Invalid" &&
          data.data.status != "Token is Expired"
        ) {
          dispatch({
            type: "SET_LOGIN_STATUS",
            payload: true,
          });
        }
        setNameUser(data.data.name);
      })
      .catch((err) => {
        // if (isLogin) {
        //   dispatch({
        //     type: "SET_LOGIN_STATUS",
        //     payload: false,
        //   });
        // }
        // console.log(err);
        console.log(err);
      });
  };

  useEffect(() => {
    getDataKategori();
    getUser();
  }, []);

  // console.log(isLogin);
  // console.log(active);
  const { Header, Footer, Content } = Layout;
  if (props.kelasSaya) {
    return (
      <Layout className={`${active ? "active-sidebar" : "deactive-sidebar"}`}>
        <Header className="header">
          <Navbar data={kategoriData} isLogin={isLogin} name={nameUser} />
        </Header>
        <Content>{props.children}</Content>
      </Layout>
    );
  }
  if (props.isLogin) {
    return (
      <Layout className={`${active ? "active-sidebar" : "deactive-sidebar"}`}>
        <Header className="header">
          <Navbar data={kategoriData} isLogin={isLogin} name={nameUser} />
        </Header>
        <Content>{props.children}</Content>
        {/* <Footer>
          <FooterC />
        </Footer> */}
      </Layout>
    );
  }
  return (
    <Layout className={`${active ? "active-sidebar" : "deactive-sidebar"}`}>
      <Header className="header">
        <Navbar data={kategoriData} isLogin={isLogin} name={nameUser} />
      </Header>
      <Content>{props.children}</Content>
      <Footer>
        <FooterC />
      </Footer>
    </Layout>
  );
}
