import React, { useState } from "react";
import Navbar from "Component/navbar";
import Cancel from "Assets/Images/cancel.png";
import Correct from "Assets/Images/correct.png";

const Transaction = () => {
  const [status, setStatus] = useState(true);
  return (
    <>
      <Navbar isOrderPage title="Transaksi" />
      <div className="section-transaction-page">
        <div className="card-transaction">
          <div className="wrapper-status">
            <h4 className="title-transaction">
              Transaksi {status ? "Berhasil" : "Gagal"}
            </h4>
            <img
              src={status ? Correct : Cancel}
              alt="status transaction"
              className="img-status"
            />
            <h3>{status ? "Selamat" : "Gagal"}</h3>
          </div>
          <div className="wrapper-order">
            <p className="order-item">No.Order: PO-0021</p>
            <p className="order-item">Practice UI for Great UI Design</p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Transaction;
