import React, { useState } from "react";
import "./navbar.scss";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { setKategori } from "Store/Action/kategoriAction";
import { useDispatch } from "react-redux";

import Axios from "axios";
import { API_URL, API } from "utils/index";

import { FaBars } from "react-icons/fa";
import { RiCloseFill } from "react-icons/ri";

// Icon
import { TiUser } from "react-icons/ti";
import { CgLogOut } from "react-icons/cg";
import { IoIosArrowBack } from "react-icons/io";

// Logo
import Logo from "Assets/Images/logo.png";

// Image
import ImageProfile from "Assets/Images/img-profile-navbar.png";
import ImgProfile from "Assets/Images/profile-pic.png";

function Navbar(props) {
  const [active, setActive] = useState(false);
  const dispatch = useDispatch();

  const _handleToggle = () => {
    // if (!active) {
    // }
    setActive(!active);
    dispatch({
      type: "SET_STATUS_SIDEBAR",
      payload: !active,
    });
  };

  const handleClickKategori = (idSlug) => {
    props.setKategori(idSlug);
    setActive(false);
    dispatch({
      type: "SET_STATUS_SIDEBAR",
      payload: false,
    });
  };
  // console.log(kategoriSlug);

  const handleShow = () => {
    _handleToggle();
  };

  const handleHide = () => {
    setActive(false);
    dispatch({
      type: "SET_STATUS_SIDEBAR",
      payload: false,
    });
  };

  const logoutApi = () => {
    Axios.post(API + "logout", props.dataUser, {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then(() => {
        localStorage.clear();
      })
      .catch((err) => console.log(err));
  };

  const handleClose = () => {
    dispatch({
      type: "SET_LOGIN_STATUS",
      payload: false,
    });
    setActive(false);
    handleHide();

    logoutApi();
  };

  // console.log(props.dataUser);

  if (props.isMateriPage) {
    return (
      <header className="header">
        <div className="nav-collapse-materi-page">
          <div className="logo-btn">
            <Link
              to="/kelas-saya/progress/semua-kelas"
              style={{ textDecoration: "none", color: "#fff" }}
            >
              <h5>
                <IoIosArrowBack /> <span className="btn-back">Kembali</span>
              </h5>
            </Link>
          </div>
          <h3 className="title-navbar">{props.title}</h3>
        </div>
      </header>
    );
  }
  if (props.isOrderPage) {
    return (
      <header className="header">
        <div className="nav-collapse-materi-page">
          <div className="logo-btn">
            <Link to="/" style={{ textDecoration: "none", color: "#fff" }}>
              <h5>
                <IoIosArrowBack /> <span className="btn-back">Kembali</span>
              </h5>
            </Link>
          </div>
          <h3 className="title-navbar">{props.title}</h3>
        </div>
      </header>
    );
  }
  if (props.isLogin) {
    return (
      <div className="nav-collapse container nav-login">
        <div className="logo-btn">
          <Link to="/" onClick={handleHide}>
            <img src={Logo} alt="" />
          </Link>
        </div>
        <button className="btn-navbar" onClick={handleShow}>
          {active ? (
            <RiCloseFill color="#ffffff" />
          ) : (
            <FaBars color="#ffffff" />
          )}
        </button>
        <div className={`navlist ${active && "navListActive"}`}>
          <div className="navbar-btn">
            <div className="navbar-btn-item item-btn-toogle">
              <button className="navbar-btn-hover">Course Category</button>
              <ul className="list-kategori-nav">
                {props.data.map((item, i) => {
                  return (
                    <li
                      className="list-kategori-nav-item"
                      key={`kategori-nav${i}`}
                    >
                      <Link
                        className="list-kategori-link"
                        to={`/kategori/${item.slug}`}
                        onClick={() => handleClickKategori(item.idcategories)}
                      >
                        <img
                          src={process.env.REACT_APP_PATH_IMAGES + item.images}
                          alt=""
                        />{" "}
                        {item.name}
                      </Link>
                    </li>
                  );
                })}
              </ul>
            </div>
            <div className="navbar-btn-item">
              <Link
                className="navbar-btn-item"
                to="/career-ready-program"
                onClick={handleHide}
              >
                Career Ready Program
              </Link>
            </div>
            <div className="navbar-btn-item">
              <Link
                className="navbar-btn-item"
                to="/about-ekselen"
                onClick={handleHide}
              >
                About
              </Link>
            </div>
          </div>
          {/* <div className="navbar-search">
            <input type="text" className="search-nav" placeholder="search" />
          </div> */}
          <div className="navbar-profile">
            <Link
              to="/kelas-saya/progress/semua-kelas"
              className="kelas-saya-navbar"
              onClick={handleHide}
            >
              Kelas Saya
            </Link>
            <button className="img-profile-navbar">
              <div className="img-wrapper-profile">
                <div className="img-wrapper">
                  <img src={ImgProfile} alt="" className="img-cover" />
                </div>
                <h4 className="title-name-profile">
                  Hi, <span>{props.name}</span>
                </h4>
              </div>
              <ul className="profile-navbar">
                <li className="profile-navbar-item">
                  <Link
                    to="/profile/edit-profile"
                    className="profile-navbar-link"
                    onClick={handleHide}
                  >
                    <TiUser /> Profile
                  </Link>
                </li>
                <li className="profile-navbar-item">
                  <Link
                    to="/"
                    className="profile-navbar-link"
                    onClick={handleClose}
                  >
                    <CgLogOut /> Keluar
                  </Link>
                </li>
              </ul>
            </button>
          </div>
        </div>
      </div>
    );
  }
  return (
    <div className="nav-collapse container">
      <div className="logo-btn">
        <Link to="/" onClick={handleHide}>
          <img src={Logo} alt="" />
        </Link>
      </div>
      <button className="btn-navbar" onClick={handleShow}>
        {active ? <RiCloseFill color="#ffffff" /> : <FaBars color="#ffffff" />}
      </button>
      <div className={`navlist ${active && "navListActive"}`}>
        <div className="navbar-btn">
          <div className="navbar-btn-item item-btn-toogle">
            <button className="navbar-btn-hover">Course Category</button>
            <ul className="list-kategori-nav">
              {props.data?.map((item, i) => {
                return (
                  <li
                    className="list-kategori-nav-item"
                    key={`kategori-nav${i}`}
                  >
                    <Link
                      className="list-kategori-link"
                      to={`/kategori/${item.slug}`}
                      onClick={() => handleClickKategori(item.idcategories)}
                    >
                      <img
                        src={process.env.REACT_APP_PATH_IMAGES + item.images}
                        alt=""
                      />{" "}
                      {item.name}
                    </Link>
                  </li>
                );
              })}
            </ul>
          </div>
          <div className="navbar-btn-item">
            <Link
              className="navbar-btn-item"
              to="/career-ready-program"
              onClick={handleHide}
            >
              Career Ready Program
            </Link>
          </div>
          <div className="navbar-btn-item">
            <Link
              className="navbar-btn-item"
              to="/about-ekselen"
              onClick={handleHide}
            >
              About
            </Link>
          </div>
        </div>
        {/* <div className="navbar-search">
          <input type="text" className="search-nav" placeholder="search" />
        </div> */}
        <div className="navbar-register">
          <Link className="btn-login" to="/login" onClick={handleHide}>
            Login
          </Link>
          <Link className="btn-register" to="/register" onClick={handleHide}>
            Register
          </Link>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    kategoriAktif: state.kategoriReducer.kategoriAktif,
    dataUser: state.userReducer.dataUser,
  };
};

const mapStateToDispatch = {
  setKategori,
};

export default connect(mapStateToProps, mapStateToDispatch)(Navbar);
