import React, { Component } from "react";
import Layout from "Component/layout/index";
import HeroDetail from "Component/heroDetail";
import KontenDetail from "Component/kontenKelasDetail";
import ListRekomendasiDetail from "Component/listRekomendasiDetailKelas";
import { connect } from "react-redux";

import DataKelas from "JSON/kelas.json";
import Axios from "axios";
import { setIdClass } from "Store/Action/kelasAction";

import { API_URL, API } from "utils/index";

class DetailPage extends Component {
  state = {
    slugDetailPage: this.props.match.params.slugDetailKelas,
    dataComment: [],
    dataKelas: {},
    dataDiscount: [],
  };

  // getComment = () => {
  //   fetch(API + "comments", {
  //     method: "GET",
  //   })
  //     .then((res) => res.json())
  //     .then((data) => this.setState({ dataComment: data }))
  //     .catch((err) => console.log(err));
  // };

  getData = (id, slug) => {
    if (id != 0) {
      Axios.get(`${API}class/${id}`)
        .then((data) => {
          this.setState({ dataKelas: { ...data.data.class[0] } });
          // console.log(data);
        })
        .catch((err) => console.log(err));
      this.getDiscount(id);
    } else {
      Axios.get(API + "class").then((data) => {
        let tempData = data.data.class.filter((item) => item.slug === slug);
        this.setState({ dataKelas: { ...tempData[0] } });
        this.props.setIdClass(tempData[0].idclass);
        this.getDiscount(tempData[0].idclass);
      });
    }
  };

  getDiscount = (id) => {
    Axios.get(API + "promotions/discounts").then((data) => {
      let newData = data.data.filter((item) => item.idclass == id);
      this.setState({ dataDiscount: newData });
    });
  };

  componentDidMount() {
    this.getData(this.props.idclass, this.state.slugDetailPage);
    // this.getComment();
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevState.slugDetailKelas != this.state.slugDetailKelas) {
      this.getData(this.props.idclass, this.state.slugDetailPage);
      // this.getComment();
    }
    if (prevProps.idclass != this.props.idclass) {
      this.getData(this.props.idclass, this.state.slugDetailPage);
      // this.getComment();
    }
  }

  formatNumber = (num) => {
    if (num == undefined) {
      return 0;
    } else {
      return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
    }
  };

  render() {
    // console.log(this.state.dataKelas);
    const { slugKelas, idclass } = this.props;
    const { dataKelas } = this.state;
    // const kelasDetail = this.getProductDetailKelas(slugKelas);
    const price = this.formatNumber(dataKelas.price);
    return (
      <Layout>
        <HeroDetail
          judul={dataKelas.name}
          rate={dataKelas.rating}
          instrukturName={dataKelas.instructor}
          instrukturRole={dataKelas.roleinstructor}
          instrukturImg={dataKelas.imagesinstructor}
          mitraName={dataKelas.namemitra}
          mitraImg={dataKelas.imagesmitra}
          demo={dataKelas.demo}
        />
        <KontenDetail
          judul={dataKelas.name}
          price={dataKelas.price}
          komen={[]}
          deskripsi={dataKelas.description}
          deskInstruktur={dataKelas.tutor}
          deskripsiMitra={dataKelas.descriptionmitra}
          id={dataKelas.idclass}
          discount={
            this.state.dataDiscount.length != 0 &&
            this.state.dataDiscount[0].potongan
          }
        />
        <ListRekomendasiDetail kategori={dataKelas.idcategories} />
      </Layout>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    slugKelas: state.kelasReducer.slugKelas,
    idclass: state.kelasReducer.idclass,
  };
};

const mapDispatchToProps = {
  setIdClass,
};

export default connect(mapStateToProps, mapDispatchToProps)(DetailPage);
