import React from "react";
import Image from "Assets/Images/empty.png";
import { Link } from "react-router-dom";

export default function EmptyClass() {
  return (
    <div className="empty-section">
      <div className="img-wrapper-empty">
        <img src={Image} alt="empty class" className="img-cover-empty" />
      </div>
      <h4>Uups kelas kosong</h4>
      <p>Silahkan kembali kehalaman awal untuk membeli kelas terlebih dahulu</p>
      <Link to="/">Home</Link>
    </div>
  );
}
