import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import Axios from "axios";
import { API_URL } from "utils/index";

export default function ListWishlist() {
  const [dataWishlist, setDataWishlist] = useState([]);
  const dataUser = useSelector((state) => state.userReducer.dataUser);

  // get data wishlist
  const getWishlist = () => {
    Axios.get(API_URL + "whislists/mywhislists", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => {
        let newData = data.data.whislists.filter(
          (item) => item.idusers == dataUser.idusers
        );
        setDataWishlist(newData);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    getWishlist();
  }, []);
  // console.log(dataWishlist[0].classes);
  return (
    <div className="list-wishlist">
      <div className="row">
        {dataWishlist.length != 0 &&
          dataWishlist.map((item) => {
            if (item.classes.length != 0) {
              return (
                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-5 col-9">
                  <div className="card-wishlist">
                    <div className="img-wrapper-wishlist">
                      <img
                        src={
                          process.env.REACT_APP_PATH_URL +
                          item.classes[0].images
                        }
                        alt={`image-${item.classes[0].name}`}
                        className="img-cover-wishlist"
                      />
                    </div>
                    <div className="card-content-wishlist">
                      <h2 className="title-wishlist">{item.classes[0].name}</h2>
                      <h4 className="instructur-wishlist">
                        {item.classes[0].instructor}
                      </h4>
                    </div>
                  </div>
                </div>
              );
            } else {
              return (
                <div>
                  <h2>Loading</h2>
                </div>
              );
            }
          })}
      </div>
    </div>
  );
}
