import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import "./index.scss";
import { Carousel, Row, Col, Layout } from "antd";
import { Link } from "react-router-dom";
import Axios from "axios";

import { API_URL, API } from "utils/index";

// Component
import Card from "Component/card";
import ListCardLoading from "./LoadingCard";

// Image
import instructorPhoto from "Assets/Images/user-testimoni.png";
import { setKategori } from "Store/Action/kategoriAction";

export default function ListRekomendasi() {
  const [idButton, setIdButton] = useState("populers");
  const [tempData, setTempData] = useState([]);
  const [dataKelas, setDataKelas] = useState([]);
  const [rekomendasi, setRekomendasi] = useState([]);
  const [highligh, setHiglight] = useState([]);
  const [discount, setDiscount] = useState([]);
  const [wishlist, setWishlist] = useState([]);
  const [kategori, setKategori] = useState([]);
  const [materies, setMateries] = useState([]);

  const dataUser = useSelector((state) => state.userReducer.dataUser);

  const handleActiveClass = (idBtn) => {
    setIdButton(idBtn);
  };

  const getScreen = () => {
    let widthScreen = window.screen.width;
    if (widthScreen < 768) {
      return 4;
    } else {
      return 2;
    }
  };

  useEffect(() => {
    getScreen();
  }, [window.screen.width]);

  const getDatKategori = () => {
    Axios.get(API + idButton)
      .then((data) => {
        let dataTemp = data.data.classTrandings;
        setRekomendasi(dataTemp);
        // console.log(data.data);
      })
      .catch((err) => console.log(err));

    Axios.get(API + "categories").then((data) => {
      let dataTemp = data.data.categories;
      setKategori(dataTemp);
    });
  };

  const getDataKelas = async () => {
    await Axios.get(API + "class").then(async (data) => {
      let newData = data.data.class;
      // console.log(data.data.class);
      setDataKelas(newData);
      // console.log("3.baru ini");
      Axios.get(API + "hilights")
        .then((data) => {
          setHiglight(data.data.hilights);
          // console.log("4.habis itu ini");
        })
        .catch((err) => console.log(err));

      Axios.get(API + "promotions/discounts")
        .then((data) => setDiscount(data.data))
        .catch((err) => console.log(err));

      Axios.get(API + "whislists/mywhislists", {
        headers: {
          Authorization: "Bearer" + localStorage.getItem("token"),
        },
      })
        .then((data) => {
          // if (dataUser != undefined) {
          // }
          let newData = data.data.whislists.filter(
            (item) => item.idusers == dataUser.idusers
          );
          setWishlist(newData);
        })
        .catch((err) => console.log(err));
    });

    // console.log(rekomendasi);
  };

  useEffect(() => {
    getDatKategori();
    getDataKelas();
    // console.log("1. baru in");
  }, [idButton, dataUser]);

  const activeClass = (classActive) => {
    return idButton === classActive ? " btn-active" : "";
  };

  let newProduk = [];
  let produk = rekomendasi.map((itemRek) =>
    dataKelas.filter((item) => {
      if (item.idclass == itemRek.idclass) {
        newProduk.push(item);
      }
    })
  );

  // const getDataKategori = (id) => {
  //   console.log(id);
  // };
  // console.log(wishlist);
  return (
    <div className="section">
      <div className="container list-rekomendasi">
        <h2 className="title">
          Rekomendasi <span className="font-weight-bold">Kelas</span>
        </h2>
        <div className="btn-kategori-a">
          <Link
            className={`btn-kategori-item ${activeClass("populers")}`}
            onClick={() => handleActiveClass("populers")}
          >
            Kelas Populer
          </Link>
          <Link
            className={`btn-kategori-item ${activeClass("newclass")}`}
            onClick={() => handleActiveClass("newclass")}
          >
            Kelas Terbaru
          </Link>
          <Link
            className={`btn-kategori-item ${activeClass("careers")}`}
            onClick={() => handleActiveClass("careers")}
          >
            Career Ready Program
          </Link>
        </div>
        {newProduk.length == 0 ? (
          <ListCardLoading />
        ) : (
          <Carousel
            className="rekomendasi-scroll"
            infinite={false}
            slidesToScroll={1}
            slidesToShow={4}
            autoplay
            dots={false}
            responsive={[
              {
                breakpoint: 1200,
                settings: {
                  slidesToShow: 3,
                  slidesToScroll: 1,
                },
              },
              {
                breakpoint: 990,
                settings: {
                  slidesToShow: 2,
                  slidesToScroll: 1,
                },
              },
              {
                breakpoint: 770,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                },
              },
            ]}
          >
            {newProduk.map((item, i) => {
              return (
                <Card
                  key={`kelas-${i}`}
                  judul={item.name}
                  instrukturPhoto={item.imagesinstructor}
                  instrukturNama={item.instructor}
                  instrukturRole={item.roleinstructor}
                  rate={item.rating}
                  jumMateri={10}
                  jumJam={item.duration}
                  harga={item.price}
                  diskon={discount.filter(
                    (itemDiscount) => itemDiscount.idclass == item.idclass
                  )}
                  benefit={highligh.filter(
                    (itemHighligh) => itemHighligh.idclass === item.idclass
                  )}
                  cover={item.images}
                  kategori={kategori.filter(
                    (itemKategori) =>
                      itemKategori.idcategories == item.idcategories
                  )}
                  slug={item.slug}
                  id={item.idclass}
                  mitraPhoto={item.imagesmitra}
                  mitraName={item.namemitra}
                  wishlist={wishlist.filter(
                    (itemWishlist) => itemWishlist.idclass == item.idclass
                  )}
                  // wishlist={false}
                />
              );
            })}
          </Carousel>
        )}
        {/* <Carousel
          infinite={false}
          slidesToScroll={1}
          slidesToShow={4}
          autoplay
          dots={false}
          className="carouselStyle"
        >
          {newProduk.length == 0 ? (
            <ListCardLoading />
          ) : (
            
          )}
        </Carousel> */}
        {/* <Carousel slidesToScroll={1} slidesToShow={4} autoplay dots={false}>
          {dataKelas.length === 0
            ? data
                .filter((item) => item.kelasPopuler === true)
                .map((itemindex0, i) => {
                  return (
                    <Card
                      key={`kelas-${i}`}
                      judul={itemindex0.judul}
                      instrukturPhoto={itemindex0.instruktur.photo}
                      instrukturNama={itemindex0.instruktur.nama}
                      instrukturRole={itemindex0.instruktur.role}
                      rate={itemindex0.rate}
                      jumMateri={itemindex0.totalMateri}
                      jumJam={itemindex0.totalJam}
                      harga={itemindex0.harga}
                      diskon={itemindex0.diskon}
                      benefit={itemindex0.benefit}
                      cover={itemindex0.imgCover}
                      kategori={itemindex0.kategori}
                      slug={itemindex0.slug}
                    />
                  );
                })
            : dataKelas.map((itemKelas, index) => {
                return (
                  <Card
                    key={`kelas-${index}`}
                    judul={itemKelas.judul}
                    instrukturPhoto={itemKelas.instruktur.photo}
                    instrukturNama={itemKelas.instruktur.nama}
                    instrukturRole={itemKelas.instruktur.role}
                    rate={itemKelas.rate}
                    jumMateri={itemKelas.totalMateri}
                    jumJam={itemKelas.totalJam}
                    harga={itemKelas.harga}
                    diskon={itemKelas.diskon}
                    benefit={itemKelas.benefit}
                    cover={itemKelas.imgCover}
                    kategori={itemKelas.kategori}
                    slug={itemKelas.slug}
                  />
                );
              })}
        </Carousel> */}
      </div>
    </div>
  );
}
