import React, { Component } from "react";
import Layout from "Component/layout/index";
import { API_URL, API } from "utils/index";

// Data
import Data from "JSON/landingPage.json";
import DataKelas from "JSON/kelas.json";

// Component
import Jumbotron from "Component/jumbotron";
import ListRekomendasi from "Component/listRekomendasi/ListRekomendasi";
import ListKategori from "Component/listKategori";
import ListBenefit from "Component/listBenefit";
import PromoBanner from "Component/promoBanner";
import ListTetsimoni from "Component/listTestimoniCard";

import axios from "axios";
import { connect } from "react-redux";
import { setUser } from "Store/Action/userAction";
import { setLoginStatus } from "Store/Action/loginAction";

class HomePage extends Component {
  state = {
    dataKategori: [],
  };

  getUser = () => {
    axios
      .get(API + "profile", {
        headers: {
          Authorization: "Bearer" + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        this.props.setUser(data.data);
        if (
          data.data.status != "Token is Invalid" &&
          data.data.status != "Token is Expired"
        ) {
          this.props.setLoginStatus(true);
        }
      })
      .catch((err) => console.log(err));
  };

  getCategories = () => {
    axios
      .get(API + "categories", {
        headers: {
          Authorization: "Bearer" + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        this.setState({ dataKategori: data.data.categories });
        // console.log(data);
      })
      .catch((err) => console.log(err));
  };

  getMateries = () => {
    axios
      .get(API_URL + "materies", {
        headers: {
          Authorization: "Bearer" + localStorage.getItem("token"),
        },
      })
      // .then((data) => console.log(data))
      .catch((err) => console.log(err));
  };

  componentDidMount() {
    this.getCategories();
    this.getUser();
    this.getMateries();
  }
  render() {
    // console.log(this.state.dataKategori);
    return (
      <Layout>
        <Jumbotron />
        <ListRekomendasi data={DataKelas.kelas} />
        <ListKategori data={this.state.dataKategori} />
        <ListBenefit />
        <PromoBanner />
        <ListTetsimoni data={Data.testimoni} />
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  return { dataUser: state.userReducer.dataUser };
};

const mapDispatchToProps = {
  setUser,
  setLoginStatus,
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);
