import React, { Component } from "react";
import { Switch, Route, NavLink } from "react-router-dom";
import Layout from "Component/layout";

// Component
import SyaratKetentuan from "Component/legal/syaratKetentuan";
import KebijakanPrivasi from "Component/legal/kebijakanPrivasi";

export default class SKPage extends Component {
  render() {
    return (
      <Layout>
        <div className="section sk-page">
          <div className="wrapper-btn-skpage container">
            <NavLink
              to="/legal/syarat-ketentuan"
              className="btn-legal"
              activeClassName="active-legal"
            >
              Syarat dan Ketentuan
            </NavLink>
            <NavLink
              to="/legal/kebijakan-privasi"
              className="btn-legal"
              activeClassName="active-legal"
            >
              Kebijakan dan Privasi
            </NavLink>
          </div>
          <Switch>
            <Route path="/legal/syarat-ketentuan" component={SyaratKetentuan} />
            <Route
              path="/legal/kebijakan-privasi"
              component={KebijakanPrivasi}
            ></Route>
          </Switch>
        </div>
      </Layout>
    );
  }
}
