import React from "react";

// Antd
import CardLoading from "Component/card/CardLoading";

export default function LoadingCard() {
  return (
    <div className="row">
      <div className="col-3">
        <CardLoading />
      </div>
      <div className="col-3">
        <CardLoading />
      </div>
      <div className="col-3">
        <CardLoading />
      </div>
      <div className="col-3">
        <CardLoading />
      </div>
    </div>
  );
}
