import React, { useEffect, useState } from "react";
import { Switch, Route, useParams } from "react-router-dom";
import { connect } from "react-redux";
import Axios from "axios";
import { API_URL, API } from "utils/index";

// Component
import Navbar from "Component/navbar";
import SideBar from "Component/sidebarMateriKelas/Index";
import Kuis from "Component/kuis/Kuis";
// import { setTypeMateri } from "Store/Action/materiAction";
import VideoPlayer from "Component/videoPlayer/VideoPlayer";

// Data
import DataKelas from "JSON/kelasSaya.json";

function MateriKelasPage(props) {
  const { slugKelas, slugMateri } = useParams();
  const { slugMateriKelas, type } = props;
  const [dataKelas, setDataKelas] = useState({
    subClass: [],
    titleClass: "",
    tempData: [],
    idClass: 0,
  });
  const [materiesData, setMateriesData] = useState([]);
  const [titleMateri, setTitleMateri] = useState("");
  const [video, setVideo] = useState("");

  const Data = Object.assign({}, ...DataKelas.kelas);

  const getVideo = (slugK) => {
    // const videoTemp = {};
    for (let i = 0; i < Data.bab.length; i++) {
      for (let j = 0; j < Data.bab[i].materi.length; j++) {
        if (slugK === Data.bab[i].materi[j].slugMateriKelas) {
          return { ...Data.bab[i].materi[j] };
        }
      }
    }
  };
  const activeSlug = () => {
    if (slugMateriKelas === "") {
      return slugMateri;
    } else {
      return slugMateriKelas;
    }
  };
  const videoTemp = getVideo(slugMateri);
  // setTypeMateri(type);

  const handleType = () => {
    return type;
  };

  // ============= FETCH

  const getIdClass = () => {
    Axios.get(API + "class")
      .then((data) => {
        let newData = data.data.class.filter((item) => item.slug == slugKelas);
        getSubClass(newData[0].idclass);
        setTitleMateri(newData[0].name);
      })
      .catch((err) => console.log(err));
  };

  const getSubClass = (idclass) => {
    Axios.get(API + "subclass")
      .then((data) => {
        let newData = data.data.subclass.filter(
          (item) => item.idclass == idclass
        );
        setDataKelas({ ...dataKelas, subClass: newData, idClass: idclass });
      })
      .catch((err) => console.log(err));
  };

  const getMateries = () => {
    Axios.get(API + "materies")
      .then((data) => {
        setMateriesData(data.data.materies);
        let videoData = data.data.materies.filter(
          (item) => item.slug === slugMateri
        );
        setVideo(videoData[0].video_720);
        // console.log(videoData);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    getIdClass();
  }, []);

  useEffect(() => {
    getMateries();
  }, [slugMateri]);
  return (
    <div className="materi-kelas">
      <Navbar isMateriPage title={titleMateri} />
      <div className="materi-kelas-konten">
        <SideBar data={dataKelas.subClass} materiesData={materiesData} />
        <div className="konten-materi-kelas">
          <Switch>
            <Route path={`/materi/video/${slugKelas}/${activeSlug()}`}>
              {/* {handleType() === "kuis" ? (
                <Kuis data={videoTemp.kuis} />
              ) : (
                <VideoPlayer video={videoTemp.video} />
                // ""
                )} */}
              {video != "" ? (
                <VideoPlayer
                  video={process.env.REACT_APP_PATH_API_VIDEO_720 + video}
                />
              ) : (
                <h2>Loading...</h2>
              )}
            </Route>
            <Route path={`/materi/kuis/${slugKelas}/kuis`}>
              <Kuis
                answears={["komputer", "mouse", "cangkir"]}
                number={1}
                question="Apa yang dimaksud maksud?"
              />
            </Route>
          </Switch>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    slugMateriKelas: state.materiReducer.slugMateriKelas,
    type: state.materiReducer.type,
  };
};

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(MateriKelasPage);
