import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import Axios from "axios";

// Component
import Navbar from "Component/navbar";

// Image
import coverClass from "Assets/Images/orderclass.png";
import coverInstructur from "Assets/Images/orderprofilepic.png";
import IconCorrect from "Assets/Images/icon-correct.png";

// Data
import PaymentData from "JSON/payment.json";
import { API_URL, API } from "utils/index";
import { useParams } from "react-router-dom";

export default function OrderPage() {
  const [idButton, setIdButton] = useState("ew");
  const [dataKelas, setDataKelas] = useState({});
  const [dataDiscount, setDataDiscount] = useState([]);
  const { orderSlug } = useParams();
  const idclass = useSelector((state) => state.kelasReducer.idclass);
  const token = useSelector((state) => state.orderReducer.token);

  // GetData Kelas
  const dataClass = (id, slug) => {
    if (id != 0) {
      Axios.get(`${API}class/${id}`)
        .then((data) => {
          // console.log(data.data.class);
          setDataKelas(data.data.class[0]);
        })
        .catch((err) => console.log(err));
      getDiscount(id);
    } else {
      Axios.get(API + "class").then((data) => {
        let tempData = data.data.class.filter((item) => item.slug === slug);
        setDataKelas(tempData[0]);
        // this.props.setIdClass(tempData[0].idclass);
        getDiscount(tempData[0].idclass);
      });
    }
  };

  const getDiscount = (id) => {
    Axios.get(API_URL + "promotions/discounts")
      .then((data) => {
        let tempData = data.data.filter((item) => item.idclass == id);
        setDataDiscount(tempData);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    dataClass(idclass, orderSlug);
    // dataDiscount();
  }, []);

  const activePayment = (payment) => {
    return idButton === payment ? "btn-active" : "";
  };

  const handleActivePayment = (idBtn) => {
    setIdButton(idBtn);
  };

  const handlePay = () => {
    window.snap.pay(token, {
      onSuccess: function (result) {
        console.log("success");
        console.log(result);
      },
      onPending: function (result) {
        console.log("pending");
        console.log(result);
      },
      onError: function (result) {
        console.log("error");
        console.log(result);
      },
      onClose: function () {
        console.log("customer closed the popup without finishing the payment");
      },
    });
  };

  const formatNumber = (num) => {
    if (num == undefined) {
      return 0;
    } else {
      return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
    }
  };

  const diskonFormat = (price, diskon) => {
    let newPrice = price - diskon;
    return formatNumber(newPrice);
  };

  const data = PaymentData.payment.filter((item) => item.method === idButton);

  console.log(dataDiscount);

  const {
    name,
    instructor,
    roleinstructor,
    imagesinstructor,
    price,
    images,
    duration,
    rating,
  } = dataKelas;

  return (
    // <Layout kelasSaya>
    <>
      <Navbar isOrderPage title="Metode Pembayaran" />
      <div className="section-order-page">
        <div className="card-order">
          <div className="row card-order-item">
            <div className="col-xl-4 col-lg-4 col-md-5">
              <div className="img-wrapper">
                <img
                  src={process.env.REACT_APP_PATH_API_IMAGES_COVER + images}
                  alt="cover class"
                  className="img-class-cover"
                />
              </div>
            </div>
            <div className="col-12 col-xl-8 col-lg-8  col-md-7">
              <div className="class-detail">
                <h4 className="title-class">{name}</h4>
                <div className="img-wrapper-cover">
                  <img
                    src={process.env.REACT_APP_PATH_API_IMAGES_COVER + images}
                    alt="cover class"
                    className="img-class-cover"
                  />
                </div>
                <div className="profile-instructur">
                  <div className="img-wrapper-profile">
                    <img
                      src={
                        process.env.REACT_APP_PATH_API_IMAGES_INSTRUCTOR +
                        imagesinstructor
                      }
                      alt="instruktur"
                      className="profile-instructur-img"
                    />
                  </div>
                  <div className="profile-instructur-item">
                    <h5 className="name-instructur">{instructor}</h5>
                    <p className="role-instructur">{roleinstructor}</p>
                  </div>
                </div>
                <div className="price-class">
                  <h5>Harga</h5>
                  {dataDiscount.length == 0 ? (
                    <>
                      <h2>IDR {formatNumber(price)}</h2>
                    </>
                  ) : (
                    <>
                      <h3>IDR {formatNumber(price)}</h3>
                      <h2>
                        IDR {diskonFormat(price, dataDiscount[0].potongan)}
                      </h2>
                    </>
                  )}
                </div>
              </div>
            </div>
          </div>
          <div className="row card-order-item justify-content-between">
            <div className="col-12 col-xl-4 col-lg-4">
              <div className="benefit-order">
                <div className="wrapper-icon-benefit">
                  <img src={IconCorrect} alt="benefit 1" />
                </div>
                <p className="benefit-order-item">
                  Akses ke materi kelas 2 tahun
                </p>
              </div>
            </div>
            <div className="col-12 col-xl-4 col-lg-4">
              <div className="benefit-order">
                <div className="wrapper-icon-benefit">
                  <img src={IconCorrect} alt="benefit 1" />
                </div>
                <p className="benefit-order-item">Certificate of Completion</p>
              </div>
            </div>
            <div className="col-12 col-xl-4 col-lg-4">
              <div className="benefit-order">
                <div className="wrapper-icon-benefit">
                  <img src={IconCorrect} alt="benefit 1" />
                </div>
                <p className="benefit-order-item">
                  Informasi update pembelajaran
                </p>
              </div>
            </div>
          </div>
          <button className="btn-payment" onClick={handlePay}>
            Checkout
          </button>
          {/* <div className="card-order-item">
            <div className="row wrapper-button-payment">
              <div className="col-auto">
                <button
                  onClick={() => handleActivePayment("ew")}
                  className={`btn-payment ${activePayment("ew")}`}
                >
                  E-Wallet
                </button>
              </div>
              <div className="col-auto">
                <button
                  onClick={() => handleActivePayment("tr")}
                  className={`btn-payment ${activePayment("tr")}`}
                >
                  Transfer Bank
                </button>
              </div>
              <div className="col-auto">
                <button
                  onClick={() => handleActivePayment("cc")}
                  className={`btn-payment ${activePayment("cc")}`}
                >
                  Kartu Kredit
                </button>
              </div>
              <div className="col-auto">
                <button
                  onClick={() => handleActivePayment("mi")}
                  className={`btn-payment ${activePayment("mi")}`}
                >
                  Minimarket
                </button>
              </div>
            </div>
            <div className="wrapper-list-payment">
              <form className="form-payment">
                {data.map((item) => {
                  return (
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="radio"
                        name="exampleRadios"
                        id="exampleRadios1"
                        value={item.method}
                      />
                      <img src={item.image} alt={item.name} />
                    </div>
                  );
                })}
              </form>
            </div>
          </div> */}
        </div>
      </div>
    </>
    // </Layout>
  );
}
