import React, { Component } from "react";
import Layout from "Component/layout/index";
import { Switch, NavLink, Route } from "react-router-dom";
import Axios from "axios";

// Image
import ProfileImage from "Assets/Images/profile-page-image.png";
import ImgProfile from "Assets/Images/profile-pic.png";
import { API_URL } from "utils/index";

export default class ProfilePage extends Component {
  state = {
    title: "Profile",
    dataUser: {},
    userProfile: {
      idusers: 0,
      firstname: "",
      lastname: "",
      tempatlahir: "",
      date_born: "",
      jobrole: "",
      address: "",
    },
    dataProfile: [],
  };

  getDataMe = () => {
    Axios.get(API_URL + "profile", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => {
        this.setState({ dataUser: { ...data.data } });
      })
      .catch((err) => console.log(err));
  };

  getDataUser = () => {
    Axios.get(API_URL + "users/profiles", {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    }).then((data) => {
      let newData = data.data.filter(
        (item) => item.idusers == this.state.dataUser.idusers
      );
      this.setState({ dataProfile: newData });
    });
  };

  handleChange = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    this.setState({
      userProfile: {
        ...this.state.userProfile,
        [name]: value,
        idusers: this.state.dataUser.idusers,
      },
    });
  };

  postDataToAPi = () => {
    Axios.post(API_URL + "users/profiles", this.state.userProfile, {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((data) => console.log(data))
      .catch((err) => console.log(err));
  };

  handleSubmit = (e) => {
    e.preventDefault();
    console.log(this.state.userProfile);
    this.postDataToAPi();
  };

  // Calender
  handleFocusCalender = (e) => {
    e.currentTarget.type = "date";
  };

  handleBlurCalender = (e) => {
    e.currentTarget.type = "text";
  };

  componentDidMount() {
    // this.getDataMe();
    this.getDataUser();
  }
  render() {
    const { dataUser, title, dataProfile } = this.state;
    // console.log(this.state.dataProfile);
    return (
      <Layout kelasSaya>
        <div className="section-profile">
          <div className="container edit-profil">
            <div className="card-profil">
              <h3 className="title-profil">Edit {title}</h3>
              <div className="button-profil">
                <NavLink
                  to="/profile/edit-profile"
                  className=" btn-profil-section"
                  onClick={() => this.setState({ title: "Profile" })}
                  activeClassName="active-class-profile"
                >
                  Profil
                </NavLink>
                <NavLink
                  to="/profile/edit-password"
                  className=" btn-profil-password"
                  onClick={() => this.setState({ title: "Kata Sandi" })}
                  activeClassName="active-class-profile"
                >
                  Password
                </NavLink>
              </div>
              <Switch>
                <Route path="/profile/edit-profile">
                  <div className="row mt-4 justify-content-center">
                    <div className="col-xl-2 col-lg-2 col-12">
                      <div className="img-profil">
                        <div className="img-wrapper">
                          <img
                            src={ImgProfile}
                            alt="profile image"
                            className="img-cover"
                          />
                        </div>
                        <button className="btn btn-change-pic" type="button">
                          Ganti
                        </button>
                      </div>
                    </div>
                    <div className="col-xl-9 col-lg-9 col-12">
                      <div className="form-edit-profil">
                        <form onSubmit={this.handleSubmit}>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Firts name
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].firstname
                                  : "First Name"
                              }
                              name="firstname"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Last name
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].lastname
                                  : "Last Name"
                              }
                              name="lastname"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Email
                            </label>
                            <input
                              type="email"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={dataUser.email}
                              disabled
                            />
                          </div>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Tempat Lahir
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].tempatlahir
                                  : "Tempat Lahir"
                              }
                              name="tempatlahir"
                              onChange={this.handleChange}
                            />
                          </div>

                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Tanggal Lahir
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].date_born
                                  : "Tanggal lahir"
                              }
                              name="date_born"
                              onChange={this.handleChange}
                              onFocus={this.handleFocusCalender}
                              onBlur={this.handleBlurCalender}
                            />
                          </div>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Pekerjaan
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].jobrole
                                  : "Pekerjaan"
                              }
                              name="jobrole"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="form-group row justify-content-between">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Alamat
                            </label>
                            <input
                              type="text"
                              className="form-control col-7 col-xl-9 col-lg-9"
                              placeholder={
                                dataProfile.length != 0
                                  ? dataProfile[0].address
                                  : "Alamat"
                              }
                              name="address"
                              onChange={this.handleChange}
                            />
                          </div>
                          <div className="row justify-content-center mt-5">
                            <button
                              className="btn btn-simpan-edit"
                              type="submit"
                              disabled={dataProfile != 0 ? true : false}
                            >
                              Simpan
                            </button>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </Route>
                <Route path="/profile/edit-password">
                  <div className="row mt-4 justify-content-between">
                    <div className="col-12">
                      <div className="form-edit-profil">
                        <form>
                          <div className="form-group row">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Kata Sandi Lama
                            </label>
                            <input
                              type="password"
                              className="form-control col-7 col-xl-8 col-lg-8"
                              placeholder="Katasandi lama"
                            />
                          </div>
                          <div className="form-group row">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Kata Sandi Baru
                            </label>
                            <input
                              type="password"
                              className="form-control col-7 col-xl-8 col-lg-8"
                              placeholder="Katasandi Baru"
                            />
                          </div>
                          <div className="form-group row">
                            <label className="col-4 col-xl-3 col-lg-3">
                              Konfirmasi Kata Sandi
                            </label>
                            <input
                              type="password"
                              className="form-control col-7 col-xl-8 col-lg-8"
                              placeholder="Konfirmasi Katasandi"
                            />
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </Route>
              </Switch>
            </div>
          </div>
        </div>
      </Layout>
    );
  }
}
