import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./index.scss";
import StarRatings from "react-star-ratings";
import { Link } from "react-router-dom";

import { AiFillHeart } from "react-icons/ai";

// Image Card
import StackLessonIcon from "Assets/Images/icon-card-kelas.png";
import ClockIcon from "Assets/Images/icon2-card-kelas.png";
import Axios from "axios";
import { API_URL, API } from "utils/index";

export default function Index(props) {
  const [materies, setMateries] = useState([]);

  const isLogin = useSelector((state) => state.loginReducer.isLogin);
  const dataUser = useSelector((state) => state.userReducer.dataUser);

  const dispatch = useDispatch();

  const orderKelas = (slugKelas, idclass, price) => {
    dispatch({
      type: "SET_SLUG_KELAS",
      payload: slugKelas,
    });
    dispatch({
      type: "SET_ID_CLASS",
      payload: idclass,
    });
    const data = {
      details: [
        {
          idclass: idclass,
          price: price,
        },
      ],
    };
    Axios.post("https://api.ekselen.id/api/orders", data, {
      headers: {
        Authorization: "Bearer" + localStorage.getItem("token"),
      },
    })
      .then((res) => {
        dispatch({
          type: "SET_TOKEN",
          payload: res.data.token,
        });
      })
      .catch((err) => console.log(err));
  };

  const setSlugKelas = (slugKelas, idclass, price) => {
    dispatch({
      type: "SET_SLUG_KELAS",
      payload: slugKelas,
    });
    dispatch({
      type: "SET_ID_CLASS",
      payload: idclass,
    });
  };

  // Get subclass Data
  const getSubClass = () => {
    Axios.get(API + "subclass")
      .then((data) => {
        let newData = data.data.subclass.filter(
          (item) => item.idclass == props.id
        );
        setMateries(newData);
        // console.log(newData);
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    getSubClass();
  }, []);

  const setHour = (n) => {
    let num = n / 60;
    let hour = Math.floor(num);

    return hour;
  };

  const jumlahJam = setHour(props.jumJam);

  const formatNumber = (num) => {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
  };

  const diskonFormat = (price, diskon) => {
    let newPrice = price - diskon;
    return formatNumber(newPrice);
  };
  // console.log(process.env.REACT_APP_PATH_API_IMAGES_COVER);
  return (
    <div className="card card-product">
      <img
        className="card-img-top"
        src={process.env.REACT_APP_PATH_API_IMAGES_COVER + props.cover}
        alt="Card image cap"
      />
      <div className="card-body">
        <h4 className="kategori-kelas text-capitalize">
          {props.kategori.length == 0 ? "" : props.kategori[0].name}
          {/* {props.kategori} */}
        </h4>
        <div className="judul-kelas">{props.judul}</div>
        <div className="instruktur-kelas-card">
          {props.mitraPhoto != null ? (
            <div className="img-wrapper">
              <img
                src={
                  process.env.REACT_APP_PATH_API_IMAGES_MITRA + props.mitraPhoto
                }
                alt="instruktur"
                className="foto-profil-card"
              />
            </div>
          ) : (
            <div className="img-wrapper">
              <img
                src={
                  process.env.REACT_APP_PATH_API_IMAGES_INSTRUCTOR +
                  props.instrukturPhoto
                }
                alt="instruktur"
                className="foto-profil-card"
              />
            </div>
          )}
          {/* <img
            src={process.env.REACT_APP_PATH_INSTRUCTOR + props.instrukturPhoto}
            alt="instruktur"
            className="foto-profil-card"
          /> */}
          <div className="instruktur-kelas">
            {props.mitraName != null ? (
              <div className="nama-instruktur-kelas">{props.mitraName}</div>
            ) : (
              <>
                <div className="nama-instruktur-kelas">
                  {props.instrukturNama}
                </div>
                <p className="pekerjaan-instruktur-kelas">
                  {props.instrukturRole}
                </p>
              </>
            )}
          </div>
        </div>
        <div className="rating-kelas">
          <div className="star-kelas">
            <StarRatings
              numberOfStars={5}
              rating={props.rate}
              starRatedColor="#FFCC47"
              starDimension="20px"
              starSpacing="2px"
            />
          </div>
          <p className="nilai-kelas">{props.rate}</p>
        </div>
        <hr />
        <div className="info-detail-kelas">
          <div className=" info-detail-kelas-item">
            <img src={StackLessonIcon} alt="" />
            <span>{materies.length} Lessons</span>
          </div>
          <div className=" info-detail-kelas-item">
            <img src={ClockIcon} alt="" />
            <span>{jumlahJam} Hour</span>
          </div>
        </div>
        <div className="info-harga-kelas justify-content-between">
          <div>
            <h4
              className={`harga-kelas ${
                props.diskon.length != 0 && "diskon-kelas"
              }`}
            >
              {formatNumber(props.harga)}
            </h4>
          </div>
          <div>
            <h4 className="harga-diskon-kelas">
              {props.diskon.length != 0
                ? diskonFormat(props.harga, props.diskon[0].potongan)
                : ""}
            </h4>
          </div>
        </div>
      </div>
      <div className="card-content-hover">
        <div className="card-hover-item">
          <h4 className="title-card-hover">Apa Yang Kamu Pelajari?</h4>
          <ul className="list-card-hover">
            {props.benefit.map((item, i) => {
              return (
                <li className="list-card-hover-item" key={`benefit-${i}`}>
                  {item.namehilights}
                </li>
              );
            })}
          </ul>
          <div className="component-button-card-hover">
            <Link
              className="btn-lihat-detail"
              to={`/kelas-detail/${props.slug}`}
              // to="/kelas-detail/example"
              onClick={() => setSlugKelas(props.slug, props.id)}
            >
              Lihat Detail
            </Link>
            <div className="button-card-hover">
              <Link
                className="mulai-belajar"
                to={`/payment/order/${props.slug}`}
                onClick={() => orderKelas(props.slug, props.id, props.harga)}
              >
                Mulai Belajar
              </Link>
              {isLogin ? (
                <button
                  className="wishlist-btn"
                  disabled={
                    props.wishlist.length != 0 &&
                    props.wishlist[0].idclass == props.id
                      ? true
                      : false
                  }
                >
                  <AiFillHeart
                    color={
                      props.wishlist.length != 0 &&
                      props.wishlist[0].idclass == props.id
                        ? "#fa3232"
                        : "#b5b0b0"
                    }
                  />
                </button>
              ) : (
                <Link className="wishlist-btn" to="/login">
                  <AiFillHeart color="#b5b0b0" />
                </Link>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
