import React from "react";
import "./index.scss";
import StarRatings from "react-star-ratings";
import ReactPlayer from "react-player";

// Image
import ImgInstruktur from "Assets/Images/instruktur-profile-hero-detail.png";
import VideoOverview from "Assets/Images/video-overview.png";
import videoDemo from "Assets/Images/5.mp4";

export default function index(props) {
  // console.log(props.judul);
  return (
    <div className="section hero-detail">
      <div className="container">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <a href="#">Home</a>
            </li>
            <li class="breadcrumb-item">
              <a href="#">Library</a>
            </li>
            <li class="breadcrumb-item active" aria-current="page">
              Data
            </li>
          </ol>
        </nav>
        <div className="row justify-content-between">
          <div className="col-12 col-xl-8 col-lg-8">
            <div className="konten-hero-detail">
              <h3>{props.judul}</h3>
              <p>
                Tingkatkan potensi penjualan dengan User Interface yang lebih
                menarik user.
              </p>
              <div className="row rating-kelas">
                <div className="col star-kelas">
                  <StarRatings
                    numberOfStars={5}
                    rating={props.rate}
                    starRatedColor="#FFCC47"
                    starDimension="20px"
                    starSpacing="2px"
                  />
                </div>
                <div className="col">
                  <h5>{props.kategori}</h5>
                </div>
              </div>
              <div className="row instruktur-profil-detail-hero ">
                <div
                  className={`${
                    props.mitraName != null
                      ? "col-6 col-xl-5 col-lg-5"
                      : "col-12 col-xl-8 col-lg-8"
                  } wrapper-instructur-profile`}
                >
                  <div className="img-wrapper">
                    <img
                      src={
                        process.env.REACT_APP_PATH_API_IMAGES_INSTRUCTOR +
                        props.instrukturImg
                      }
                      alt=""
                      className="instruktur-profil-detail-hero-img"
                    />
                  </div>
                  <div className="meta-conten">
                    <h4>{props.instrukturName}</h4>
                    <p>{props.instrukturRole}</p>
                  </div>
                </div>
                {props.mitraName != null && (
                  <div className="col-6 col-xl-5 col-lg-5 wrapper-instructur-profile">
                    <div className="img-wrapper">
                      <img
                        src={
                          process.env.REACT_APP_PATH_API_IMAGES_MITRA +
                          props.mitraImg
                        }
                        alt={`${props.mitraName}-logo`}
                        className="instruktur-profil-detail-hero-img"
                      />
                    </div>
                    <div className="meta-conten">
                      <h4>{props.mitraName}</h4>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
          <div className="col">
            <ReactPlayer
              url={`https://ekselen-storage.s3-ap-southeast-1.amazonaws.com/demo/${props.demo}`}
              width="360px"
              height="205px"
              wrapper="wrapperVid"
              controls
            ></ReactPlayer>
          </div>
        </div>
      </div>
    </div>
  );
}
