import React from "react";
import Layout from "Component/layout";

import { FaPhoneSquare } from "react-icons/fa";
import { MdEmail } from "react-icons/md";
import Image from "Assets/Images/contact.png";

export default function ContactPage() {
  return (
    <Layout>
      <div className="section-contact">
        <h2 className="title-contact">Contact Us</h2>
        <div className="row">
          <div className="col-12 col-xl-6 col-lg-6 col-md-6">
            <form className="form-contact">
              <h4 className="form-title">Anda memiliki pertanyaan?</h4>
              <div className="form-group ">
                <label>Nama Lengkap</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Nama Lengkap"
                />
              </div>
              <div className="form-group ">
                <label>Email</label>
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                />
              </div>
              <div className="form-group ">
                <label>Nomor Telepon</label>
                <input
                  type="tel"
                  className="form-control"
                  placeholder="Nomor Telepon"
                />
              </div>
              <div className="form-group ">
                <label>Subjek</label>
                <input type="text" className="form-control" />
              </div>
              <div className="form-group ">
                <label>Pertanyaan</label>
                <input type="text" className="form-control" />
              </div>
            </form>
          </div>
          <div className="col-12 col-xl-6 col-lg-6 col-md-6">
            <div className="wrapper-contact">
              <div className="img-wrapper">
                <img src={Image} alt="contact image" className="img-cover" />
              </div>
              <hr />
              <div className="wrapper-contact-item">
                <div className="wrapper-item">
                  <h5 className="wrapper-item">Alamat</h5>
                  <p className="desc-item">
                    Menara Thamrin Level 3A, #3A09, Jl. M.H. Thamrin No.3,
                    Jakarta 10250
                  </p>
                </div>
                <div className="wrapper-item">
                  <h5 className="wrapper-item">Contact</h5>
                  <p className="desc-item">
                    <FaPhoneSquare style={{ fontSize: "20px" }} />{" "}
                    <a
                      href="tel:+622129830175"
                      style={{ textDecoration: "none", color: "#2f3c80" }}
                    >
                      021 2983 0175
                    </a>
                  </p>
                  <p className="desc-item">
                    <MdEmail style={{ fontSize: "20px" }} />{" "}
                    <a
                      href="mailto:admin@ekselen.id"
                      style={{ textDecoration: "none", color: "#2f3c80" }}
                    >
                      admin@ekselen.id
                    </a>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}
