import React, { useState } from "react";
import "./index.scss";

export default function Kuis(props) {
  const { data, number, question, answears, title } = props;
  const [jawaban, setJawaban] = useState("");
  const handleChange = (e) => {
    setJawaban(e.target.value);
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(jawaban);
  };
  return (
    <div className="kuis">
      <h2 className="title-kuis">Kuis {title}</h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group-kuis">
          <label className="pertanyaan">{`${number}. ${question}`}</label>
          <div className="form-answear-inline">
            {answears.map((answear, index) => {
              return (
                <div class="form-check form-check-inline">
                  <input
                    class="form-check-input"
                    type="radio"
                    value={answear}
                    name="jawaban"
                    onChange={handleChange}
                  />
                  <label class="form-check-label" for="defaultCheck1">
                    {answear}
                  </label>
                </div>
              );
            })}
          </div>
          {/* {data.map((item, i) => {
          return (
            <>
              <label className="pertanyaan">
                {`${item.id}. ${item.question}`}
              </label>
              <div className="form-answear-inline">
                {item.answears.map((answear, index) => {
                  return (
                    <div class="form-check form-check-inline">
                      <input
                        class="form-check-input"
                        type="radio"
                        value=""
                        name={`kuis${item.id}`}
                      />
                      <label class="form-check-label" for="defaultCheck1">
                        {answear}
                      </label>
                    </div>
                  );
                })}
              </div>
            </>
          );
        })} */}
        </div>
        <button type="submit">Submit</button>
      </form>
    </div>
  );
}
