import React, { useState } from "react";
import "./index.scss";
import { Link, useParams } from "react-router-dom";
import { connect } from "react-redux";
import { setTypeMateri, setMateriSlug } from "Store/Action/materiAction";

// Icons
import { TiArrowSortedDown, TiArrowSortedUp } from "react-icons/ti";

function SidebarBtn(props) {
  const [idButton, setIdButton] = useState("");
  const [isOpen, setIsOpen] = useState(false);

  const { slugKelas, slugMateri } = useParams();

  const handleClikId = () => {
    setIsOpen(!isOpen);
    return setIdButton(idButton === "" ? "sidebar-isopen" : "");
  };

  const handleType = (typeMateri, slugMateri) => {
    props.setTypeMateri(typeMateri);
    props.setMateriSlug(slugMateri);
  };

  const { title, materi, idSubclass } = props;
  const dataMateri = materi?.filter((item) => item.idsubclass === idSubclass);
  if (props.type === "kuis") {
    return (
      <div className="sidebar-bab">
        <button className="sidebar-bab-btn">
          <Link
            className="sidebar-kuis-btn"
            to={`/materi/kuis/${slugKelas}/kuis`}
          >
            {title}
          </Link>
        </button>
      </div>
    );
  }
  return (
    <div className="sidebar-bab">
      <button
        className={`sidebar-bab-btn ${isOpen && "sidebar-bab-btn-active"}`}
        type="button"
        onClick={handleClikId}
      >
        {title}{" "}
        <span className="icon-sidebar">
          <TiArrowSortedUp color="#2f3c80" />
          {/* {isOpen ? (
          ) : (
            <TiArrowSortedDown color="#2f3c80" />
          )} */}
        </span>
      </button>
      <ul className={`sidebar-subbab ${idButton}`}>
        {dataMateri?.map((item, i) => {
          return (
            <li
              className="sidebar-subbab-item"
              key={`list-materi-kelas-${item.idmateries}`}
            >
              <Link
                className={`sidebar-subbab-link ${
                  item.slug === slugMateri ? "link-active" : ""
                }`}
                to={`/materi/video/${slugKelas}/${item.slug}`}
              >
                {item.name_materi}
              </Link>
            </li>
          );
        })}
      </ul>
    </div>
  );
}

const mapDispatchToProps = {
  setTypeMateri,
  setMateriSlug,
};

export default connect(null, mapDispatchToProps)(SidebarBtn);
